.menu
  display: none
  @media (min-width: 768px)
    display: flex
    align-items: center
    justify-content: space-between

.link
  display: block
  font-size: 14px
  font-weight: var(--semibold-fw)
  text-transform: uppercase
  transition: all 0.3s ease
  @media (min-width: 768px)
    padding: 12.5px 0 15px
    font-size: 16px
  &:hover
    color: var(--taffy-pink)

.submenu
  background-color: var(--white)
  position: absolute
  left: 0
  right: 0
  top: 116px
  opacity: 0
  visibility: hidden
  z-index: -999
  transition: opacity 0.5s ease, visibility 0.5s ease, transform 0.5s ease 0.1s
  transform: translateY(-50px)
  @media (min-width: 1000px)
    top: 118px
  @media (min-width: 1440px)
    top: 110px

.content
  display: flex
  align-items: center
  justify-content: center
  gap: 24px
  padding: 10px 85px
  border-bottom: 1px solid var(--grey)
  max-width: 735px
  margin: 0 auto
  @media (min-width: 1000px)
    gap: 36px
    padding: 10px 115px
    max-width: 900px
  @media (min-width: 1440px)
    gap: 70px
    padding: 15px 95px
    max-width: 1140px

.submenuLink
  text-transform: capitalize
  font-size: 14px
  font-weight: var(--regular-fw)
  white-space: nowrap
  transition: all 0.3s ease
  &:hover
    color: var(--taffy-pink)
  @media (min-width: 1440px)
    font-size: 16px

.menu li:hover .submenu
  opacity: 1
  visibility: visible
  z-index: 3
  transition-delay: 0.3s
  transform: translateY(0)

.active
  color: var(--taffy-pink)
  font-style: italic
