.cart
  display: none
  @media (min-width: 768px)
    display: block
    width: 20px
    height: 20px
    margin-left: 40px
    cursor: pointer
    svg
      stroke: var(--umbra)
      width: 100%
      height: 100%
      transition: all 0.3s ease
      &:hover
        stroke: var(--taffy-pink)
  @media (min-width: 1000px)
    margin-left: 55px
  @media (min-width: 1440px)
    width: 24px
    height: 24px
    margin-left: 33px
